<template>
  <div class="manage">
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item><router-link to="/admin/home">首页</router-link></a-breadcrumb-item>
        <a-breadcrumb-item>书籍管理</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <div class="serch_field">
      <a-form-model
        layout="inline"
      >

        <a-form-model-item style="width: 100%;">
          <a-form-model-item>
            <a-input
              placeholder="请输入书籍名称、作者、编号..."
              v-model="params.keyword"
              style="width: 220px;"
            ></a-input>
          </a-form-model-item>
          <a-button type="primary" @click="search" style="margin-right: 12px;">搜索</a-button>
          <a-button @click="reset">重置</a-button>
        </a-form-model-item>

        <a-form-model-item style="width: 100%">
          <a-form-model-item label="书籍分类">
            <a-select
              v-model="params.first_category_id"
              :options="firstCateOptions"
              placeholder="一级分类"
              allowClear
            ></a-select>
          </a-form-model-item>

          <a-form-model-item>
            <a-select
              v-model="params.second_category_id"
              :options="secondCateOptions"
              placeholder="二级分类"
              allowClear
            ></a-select>
          </a-form-model-item>

          <a-form-model-item label="书籍状态">
            <a-select
              v-model="params.book_status"
              :options="book_status"
              allowClear
            ></a-select>
          </a-form-model-item>  
        </a-form-model-item>

        <a-form-model-item>
          <a-button type="primary" style="margin-right: 20px;" @click="createBook">创建书籍</a-button>
          <a-button type="primary">批量导入</a-button>
        </a-form-model-item>

      </a-form-model>
    </div>
    
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="first_cate" slot-scope="text">
        {{ text.firstCate && text.firstCate.category_name }}
      </template>
      <template slot="second_cate" slot-scope="text">
        {{ text.secondCate && text.secondCate.category_name }}
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="viewBookDetail(text.id)">查看</a-button>
        <a-button type="link" @click="editBookDetail(text.id)">编辑</a-button>
        <a-button type="link" @click="offBook(text.id)">下架</a-button>
        <a-button type="link" @click="deleteBook(text.id)">删除</a-button>
      </template>
    </s-table>

    <!-- 查看书籍详情 -->
    <a-modal
      v-model="bookDetailVisible"
      :footer="null"
      title="书籍详情"
      width="50%"
    >
      <h2>基本信息</h2>
      <a-row
        type="flex"
        class="book_info"
      >
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍名称：</span>
          <span>{{ bookDetail.book_name }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍作者：</span>
          <span>{{ bookDetail.author }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>一级分类：</span>
          <span>{{ bookDetail.firstCate ? bookDetail.firstCate.category_name : "" }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>二级分类：</span>
          <span>{{ bookDetail.secondCate ? bookDetail.secondCate.category_name : "" }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>馆藏量：</span>
          <span>{{ bookDetail.stock + '/' + bookDetail.total }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍编码：</span>
          <span>{{ bookDetail.book_code }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍位置：</span>
          <span>{{ bookDetail.place }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍状态：</span>
          <span>{{ bookDetail.status == 1 ? '正常' : '下架' }}</span>
        </a-col>
      </a-row>
      <h2>借阅记录</h2>
      <div>
        <a-row>
          <a-col :span="6">时间</a-col>
          <a-col :span="6">用户</a-col>
          <a-col :span="6">操作</a-col>
          <a-col :span="6">状态</a-col>
        </a-row>
        <a-row>
          <a-col :span="6">2021-1-5 21:47</a-col>
          <a-col :span="6">admin</a-col>
          <a-col :span="6">归还</a-col>
          <a-col :span="6">正常</a-col>
        </a-row>
      </div>
    </a-modal>

  </div>
</template>
<script>
import { Breadcrumb, FormModel, Select, Modal } from 'ant-design-vue'
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'BookManage',
  components: { 
    ABreadcrumb: Breadcrumb, 
    ABreadcrumbItem: Breadcrumb.Item, 
    AFormModel: FormModel, 
    AFormModelItem: FormModel.Item,
    ASelect: Select,
    STable,
    AModal: Modal
  },
  data() {
    let book_status = [
      {
        label: '全部',
        value: 0
      },
      {
        label: '上架',
        value: 1
      },
      {
        label: '下架',
        value: 2
      }
    ]

    let params = {
      keyword: '',
      first_category_id: undefined,
      second_category_id: undefined,
      book_status: 0
    }

    let columns = [
      {
        title: 'id',
        dataIndex: 'id',
      },
      {
        title: '书籍名称',
        dataIndex: 'book_name',
      },
      {
        title: '作者',
        dataIndex: 'author',
      },
      {
        title: '一级分类',
        scopedSlots: { customRender: "first_cate"},
      },
      {
        title: '二级分类',
        scopedSlots: { customRender: "second_cate"},
      },
      {
        title: '馆藏量',
        dataIndex: 'stock',
      },
      {
        title: '书籍编号',
        dataIndex: 'book_code',
      },
      {
        title: '书籍位置',
        dataIndex: 'place',
      },
      {
        title: '状态',
        dataIndex: '',
      },
      {
        title: '操作',
        width: '250px',
        scopedSlots: { customRender: "action"},
      },
    ]    

    return {
      book_status,
      params,
      columns,
      bookDetailVisible: false,
      loadData: paramters => {
        return this.getBooksList(Object.assign({}, this.params, paramters))
      }
    }
  },
  computed: {
    ...mapState('booksManage', ['allCategoryList', 'bookDetail']),
    secondCateOptions() {
      return this.allCategoryList && this.allCategoryList.second && this.allCategoryList.second.map(item => {
          return {
            label: item.category_name,
            value: item.id
          }
        })
    },
    firstCateOptions() {
      return this.allCategoryList && this.allCategoryList.first && this.allCategoryList.first.map(item => {
          return {
            label: item.category_name,
            value: item.id,
          }
        })
    }
  },
  methods: {
    ...mapActions('booksManage', {
      getBooksList: 'getBooks',
      delBook: 'deleteBook',
      getBookDetail: 'getBookDetail' 
    }),
    viewBookDetail(id) {
      this.getBookDetail(id).then(res => {
        this.bookDetailVisible = true
      })
      
    },
    editBookDetail(id) {
      this.$router.push({
        name: 'Book Detail',
        params: {
          id
        }
      })
    },
    offBook(id) {

    },
    deleteBook(id) {
      const _this = this
      this.$confirm({
        title: '删除书籍',
        content: '你确定要删除些书籍？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.delBook(id).then(res => {
            _this.$message.success('删除成功')
            _this.$refs.table.refresh(true)
          })
        },
      })
    },
    search() {
      this.$refs.table.refresh(true)
    },
    reset() {
      this.params = {
        keyword: '',
        first_category_id: undefined,
        second_category_id: undefined,
        book_status: 0
      }
      this.$refs.table.refresh(true)
    },
    createBook() {
      this.$router.push({
        name: 'Book Create'
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.manage {
  .ant-select {
    width: 174px;
  }
  .ant-table {
    .ant-btn {
      padding: 0 8px;
    }
  }
  .ant-form {
    >.ant-form-item {
      margin-bottom: 5px;
    }
  }
}
.book_info {
  .ant-col {
    margin-bottom: 15px;
  }
}
</style>
